@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap);
.icon-button{outline:none;width:2.5rem;height:2.5rem;border-radius:50%;font-weight:600;display:flex;align-items:center;justify-content:center;font-size:1rem;cursor:pointer;background-color:transparent;border:1px solid var(--color-gray-700);transition:0.25s}.icon-button:hover{border-color:var(--color-primary-500)}.icon-button:hover svg{fill:var(--color-primary-500)}.icon-button svg{fill:var(--color-white-two);width:16px;height:16px}.icon-button-container{position:relative}.icon-button-container:hover .icon-button-tooltip{opacity:1;visibility:visible}.icon-button-tooltip{z-index:1;opacity:0;visibility:hidden;position:absolute;top:-2.75rem;right:calc(-50% - 1px);border-radius:1px;padding:0.375rem 0.75rem;font-size:0.75rem;font-family:var(--font);color:var(--color-white-two);background-color:var(--color-gray-300);border:1px solid var(--color-gray-700);border-radius:2px;white-space:nowrap;box-shadow:0 2px 6px var(--color-black-three)}.icon-button-tooltip::after{bottom:-6px;left:calc(50% - 6px);position:absolute;content:"";width:12px;height:6px;background-color:var(--color-gray-700);-webkit-clip-path:polygon(50% 100%, 0 0, 100% 0);clip-path:polygon(50% 100%, 0 0, 100% 0)}

.home-title{margin:0;font-size:3rem;font-weight:800;color:var(--color-white-two);text-align:center}.home-description{margin:0 0 2rem;color:var(--color-gray-900);font-size:1.125rem;text-align:center}.converters{display:flex;flex-wrap:wrap;grid-gap:1.5rem;gap:1.5rem}.converter-card{flex:1 1;padding:1.5rem;min-width:16rem;height:16rem;width:100%;background-color:var(--color-gray-200);text-decoration:none;border-radius:2px;position:relative;display:flex;flex-direction:column;justify-content:space-between;overflow:hidden}.converter-card .icon-button svg{fill:none}.converter-card:hover .abstract-line>path{stroke:var(--color-primary-500)}.converter-card:hover .icon-button{border-color:var(--color-primary-500)}.converter-card:hover .icon-button svg>path{stroke:var(--color-primary-500) !important}.converter-card .abstract-line{height:100%;top:0;right:0;position:absolute}.converter-card .abstract-line-one{top:-4px;right:-4px}.converter-card .abstract-line-two{right:-6px}.converter-card h2{white-space:nowrap;color:var(--color-white-two);margin:0}.converter-card p{margin:1rem 0 0;color:var(--color-gray-900)}

.progress-bar{height:4px;width:100%;max-width:16rem;background-color:var(--color-gray-600);border-radius:8px;transition:all 0.15s ease;position:relative;display:flex;overflow:hidden}.progress-bar-line{border-radius:8px;width:40%;height:100%;background-color:var(--color-primary-500);position:absolute;transition:width 0.25s;-webkit-animation:loading 1.5s ease-in-out infinite;animation:loading 1.5s ease-in-out infinite;left:-20%}.progress-bar-container{margin:0.5rem;width:100%;height:8px;background-color:transparent;height:4rem;text-align:center;display:flex;flex-direction:column;align-items:center}.progress-bar-container-progress{margin:0 0 0.5rem;font-weight:500;font-size:0.875rem}.progress-bar-txt{color:var(--color-gray-900);margin:0.5rem 0 0;font-size:0.875rem}@-webkit-keyframes loading{0%{left:-20%}50%{left:80%}100%{left:-20%}}@keyframes loading{0%{left:-20%}50%{left:80%}100%{left:-20%}}

.ocr-file-upload{display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%;padding:0 1rem;height:16rem;position:relative;border:2px dashed var(--color-gray-600);background-color:var(--color-gray-200)}.ocr-file-upload-container{display:flex;flex-direction:column;align-items:center;text-align:center}.ocr-file-upload-container-txt-title{font-size:1.25rem;color:var(--color-white-two);margin:0.625rem 0}.ocr-file-upload-container-txt-desc{color:var(--color-gray-900);margin:0;line-height:1.35;font-size:0.875rem}.ocr-file-upload .icon-file-upload,.ocr-file-upload .icon-file-upload-success{width:3rem;height:3rem;fill:var(--color-white-one)}.ocr-file-upload-input{cursor:pointer;opacity:0;position:absolute;height:100%;width:100%}.ocr-file-upload-input:disabled{cursor:not-allowed}

.text-wrapper{display:flex;flex-direction:column;align-items:flex-end;width:100%;position:relative;min-height:11rem}.text-wrapper-heading{align-self:flex-start;margin-bottom:0.5rem;color:var(--color-gray-900);font-weight:500}.text-wrapper-actions{display:flex;grid-gap:1rem;gap:1rem;margin-top:1rem}.text-wrapper-output{background-color:var(--color-gray-200);border:2px solid var(--color-gray-600);padding:1rem;height:100%;width:100%;border-radius:1px;box-sizing:border-box;resize:none;font-family:var(--font);font-size:0.875rem;font-weight:400;overflow-wrap:break-word}.text-wrapper-output:disabled{color:var(--color-white-one)}.text-wrapper-output:focus{outline:none}

.btn{border-radius:10rem;font-weight:600;padding:0.5rem 2rem;outline:none;border:none;font-family:var(--font);background:var(--color-primary-500);font-weight:500;font-size:0.875rem;cursor:pointer;white-space:nowrap;color:var(--color-black-one)}.btn-full-width{width:100%}.btn:disabled{cursor:not-allowed}.btn:hover:not(:disabled){background-color:var(--color-primary-600)}

.option-active{font-weight:600;color:var(--color-primary-500);background-color:var(--color-gray-500)}.languages-select{position:relative;cursor:pointer}.languages-select .selected-language{border-bottom:2px solid var(--color-gray-700);width:14rem;padding:0 0 0.5rem;border-radius:2px;display:flex;align-items:center;justify-content:space-between;color:var(--color-white);font-size:0.875rem}.languages-select .selected-language .icon-caret-down>path{stroke:var(--color-gray-700)}.languages-select ul{top:3rem;position:absolute;list-style:none;border:2px solid var(--color-gray-600);background-color:var(--color-gray-200);border-radius:2px;overflow-y:auto;margin:0;padding:0;width:14rem;height:13rem;z-index:1;box-shadow:0 2px 6px var(--color-black-three);font-size:0.875rem}.languages-select li{padding:0.625rem 1rem}@media screen and (max-width: 512px){.languages span{display:none}}@media screen and (max-width: 414px){.languages,.languages-select,.selected-language,ul{min-width:100%}}

.error{margin:1rem 0;display:flex;align-items:center;justify-content:space-between;font-size:1rem;width:100%;background-color:hsla(var(--color-hue), 36%, 54%, 0.25);border:1px solid var(--color-primary-500);border-radius:2px;padding:0.5rem 1rem}.error-message{color:var(--color-primary-900);display:flex;align-items:center;grid-gap:1rem;gap:1rem;font-size:0.875rem}.error .icon-close{fill:var(--color-primary-900) !important;cursor:pointer}.error .icon-warning-triangle>path{stroke:var(--color-primary-900)}

.breadcrumbs{display:flex;align-items:center;grid-gap:0.5rem;gap:0.5rem}.breadcrumbs .breadcrumb{color:var(--color-white-two)}.breadcrumbs .breadcrumb-divider{color:var(--color-gray-900) !important}.breadcrumbs .breadcrumb-active{color:var(--color-primary-500);text-transform:capitalize}

.main-content{display:flex;flex-direction:column;justify-content:space-between;grid-gap:1.5rem;gap:1.5rem;max-width:48rem;width:100%}.image-url-input{font-family:var(--font);color:var(--color-white-one);padding:0 0 0.5rem;border:none;outline:none;border-bottom:2px solid var(--color-gray-700);font-size:0.875rem;caret-color:var(--color-primary-700);background-color:transparent;transition:0.25s}.image-url-input::-webkit-input-placeholder{color:var(--color-gray-800)}.image-url-input:-ms-input-placeholder{color:var(--color-gray-800)}.image-url-input::placeholder{color:var(--color-gray-800)}.image-url-input:focus{border-color:var(--color-white-three)}.input-and-languages{display:flex;flex-wrap:wrap;grid-gap:1.5rem;gap:1.5rem;margin-bottom:0.5rem}.input-and-languages input{flex:1 1}.separator{text-align:center;color:var(--color-gray-900);position:relative;align-self:center;width:100%;font-size:0.875rem}.separator::before,.separator::after{top:50%;position:absolute;content:"";width:45%;height:1px;background-color:var(--color-gray-600)}.separator::before{left:0}.separator::after{right:0}

.cyrilic-to-latin-converter{display:flex;flex-direction:column;grid-gap:1.5rem;gap:1.5rem}.cyrilic-to-latin-converter-textarea{padding:1rem;border-radius:2px;resize:none;background-color:var(--color-gray-200);border:2px solid var(--color-gray-600);color:var(--color-white-one);outline:none;width:100%;caret-color:var(--color-primary-500);font-family:var(--font);font-size:0.875rem;font-weight:400}.cyrilic-to-latin-converter-textarea-label{display:block;font-weight:500;color:var(--color-gray-900);margin-bottom:0.5rem}

.App{margin:auto;max-width:48rem;padding:2rem}

html,body{margin:0;padding:0;font-family:"Inter", sans-serif;font-family:var(--font);color:hsl(0, 0%, 99%);color:var(--color-white-one);background-color:hsl(214, 8%, 12%);background-color:var(--color-gray-100);background-image:radial-gradient(hsl(214, 6%, 15%) 1px, transparent 1px);background-image:radial-gradient(var(--color-gray-300) 1px, transparent 1px);background-size:0.5rem 0.5rem}*{box-sizing:border-box}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-thumb{background:hsl(214, 6%, 20%);background:var(--color-gray-600);border-radius:1rem}b{color:var(--color-white)}hr{border-color:var(--color-gray-00);border-top:0;margin:0;padding:0}:root{--color-hue: 180;--color-black-one: hsl(0, 0%, 0%);--color-black-two: hsl(0, 0%, 4%);--color-black-three: hsl(0, 0%, 6%);--color-white-one: hsl(0, 0%, 99%);--color-white-two: hsl(0, 0%, 86%);--color-white-three: hsl(0, 0%, 76%);--color-gray-900: hsl(214, 6%, 50%);--color-gray-800: hsl(214, 6%, 42%);--color-gray-700: hsl(214, 6%, 30%);--color-gray-600: hsl(214, 6%, 20%);--color-gray-500: hsl(214, 6%, 18%);--color-gray-400: hsl(214, 6%, 16%);--color-gray-300: hsl(214, 6%, 15%);--color-gray-200: hsl(214, 8%, 14%);--color-gray-100: hsl(214, 8%, 12%);--color-primary-900: hsl(var(--color-hue), 36%, 66%);--color-primary-800: hsl(var(--color-hue), 36%, 62%);--color-primary-700: hsl(var(--color-hue), 36%, 58%);--color-primary-600: hsl(var(--color-hue), 36%, 54%);--color-primary-500: hsl(var(--color-hue), 36%, 50%);--color-primary-400: hsl(var(--color-hue), 36%, 46%);--color-primary-300: hsl(var(--color-hue), 36%, 42%);--color-primary-200: hsl(var(--color-hue), 36%, 38%);--color-primary-100: hsl(var(--color-hue), 36%, 34%);--font: "Inter", sans-serif}

